<script>
import { getsuperes, getxiangqing, updatestatus } from "../../api/html.js";
export default {
    data() {
        return {
            // 数据渲染
            kong: [],
            id: "",
            sto: "",
        }
    },
    methods: {
        // 修改状态
        xiugai() {
            let a = !this.$store.state.arr.status
            let obj = {
                id: this.$store.state.arr.id,
                status: a ? 1 : 0,
            }
            updatestatus(obj)
                .then(response => {
                    console.log('状态改变成功', response);
                    this.$store.dispatch('uparr', a ? 1 : 0)
                    this.show();
                    this.$emit("a")
                })
                .catch(error => {
                    console.error('状态改变失败', error);
                });
        },
        // 数据渲染
        show() {
            getsuperes().then(res => {
                this.kong = res.data.data
            })
        },
        // 详情信息
        getxiangqing() {
            getxiangqing().then(res => {
                console.log(res)
            })
        },
    },
    created() {
        this.show()
    }
}
</script>
